body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header-logo {
  width: 50px;
  height: auto;
}

.searchbox {
  background-color: #fff;
  padding: 10px;
}

.container {
  height: calc(100vh - 100px);
  display: flex;
  overflow: hidden;
}

.sidebar {
  box-sizing: border-box;
  background-color: #f4f4f4;
  flex-shrink: 0;
  width: 20%;
  padding: 20px;
}

.main-content {
  box-sizing: border-box;
  background-color: #90ee90;
  flex-direction: column;
  flex: 1;
  padding: 10px;
  display: flex;
  overflow-y: auto;
}

#hits {
  flex-grow: 1;
  width: 100%;
  padding-right: 20px;
  overflow-y: auto;
}

.ais-Hits-list {
  flex-wrap: wrap;
  gap: 10px;
  margin: 0;
  padding: 0 10px 10px;
  list-style: none;
  display: flex;
}

.ais-Hits-item {
  box-sizing: border-box;
  background-color: #ff0;
  border: 2px solid #00f;
  flex: 0 0 calc(33.333% - 10px);
  min-width: 200px;
  max-width: calc(33.333% - 10px);
}

.hit {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #ddd;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
}

.hit-image {
  object-fit: cover;
  width: 100%;
  height: auto;
}

.hit-content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  display: flex;
}

#pagination {
  margin-top: 20px;
}
